import React, { useEffect, useState } from 'react';
import crypto from 'crypto';
import { likeCommentInTweet } from '../Actions/postActions';
import { useDispatch } from 'react-redux';
import { unLikeCommentInTweet } from '../Actions/postActions';

const Comment = ({ profilePictureOfCommenter, commenterName, date, message, likes, userId, tweetId, commentId, media, setJustOnePonce }) => {

    const [isLiked, setIsLiked] = useState("")
    const [myId,setMyID] = useState(crypto.randomBytes(4).toString('hex'));
    const [heartColor, setHeartColor] = useState("#BDBDBD")

    const dispatch = useDispatch()

    const [theTime,setTheTime] = useState("")
    useEffect(() => {
        let days1 = Date.now() / 1000 / 60 / 60 / 24;
        let days2 = date / 1000 / 60 / 60 / 24;
    
        let time = days1 - days2;
        
        if(typeof date === "string"){
            setTheTime(date)
        }else{
            let theDate = new Date(date)
            if (time < 1) {
                let hourC = theDate.toLocaleString().split(",")[1].split(" ")[1].split(":")
                let hourR = hourC[0] + ":" + hourC[1]
        
                setTheTime("today at " + hourR)
            } else if (time >= 1) {
                let toDateString = theDate.toDateString().split(" ")
                let day = ""
                if (toDateString[0] === "Mon") {
                    day = "Monday"
                } else if (toDateString[0] === "Tue") {
                    day = "Tuesday"
                } else if (toDateString[0] === "Thu") {
                    day = "Thursday"
                } else if (toDateString[0] === "Sat") {
                    day = "Saturday"
                } else if (toDateString[0] === "Fri") {
                    day = "Friday"
                } else if (toDateString[0] === "Sun") {
                    day = "Sunday"
                } else if (toDateString[0] === "Wed") {
                    day = "Wednesday"
                }
                let hourC = theDate.toLocaleString().split(",")[1].split(" ")[1].split(":")
                let hourR = hourC[0] + ":" + hourC[1]
        
                setTheTime(day + " " + toDateString[2] + " " + toDateString[1] + " at " + hourR)
            }
        
        }
        likes.forEach((like) => {
            let thisComment = document.getElementById(myId)
            if (like === userId) {
                let likeFunc = thisComment.children[0].children[1].children[1].children[0];
                likeFunc.children[1].textContent = "Liked"
                setHeartColor("#EB5757")
                setIsLiked("liked")
            }
        })
    }, [isLiked])

   
    return (
        <>
            <div className="comment" id={myId}>
                <div className="principaleFunc">
                    <div className="profilePictureOfCommenter">
                        <img className="profilePicture" src={profilePictureOfCommenter} alt="profilePictureOfCommenter" />
                    </div>
                    <div className="outOfPictureYouNow">
                        <div className="TheMessageOfCommente">
                            <div className="TheHeadPartOfComment">
                                <p className="commentNameOfComment">{commenterName}</p>
                                <p className="griser">{theTime}</p>
                            </div>
                            <div className="contentOfComment">
                                <p>{message}</p>
                            </div>
                            {media!==null && <div className = "potontielImage" ><img className = "basicImageInPost" src = {media+""} alt = "media" /></div>}
                        </div>
                        <div className="supFunctions">
                            <div className={`likeFunc btn ${isLiked}`} onClick={() => {
                                //("la function de like est appel")
                                let thisComment = document.getElementById(myId).children[0].children[1].children[1].children[0];
                                if (thisComment.classList[2] === "liked") {
                                    thisComment.classList.remove("liked")
                                    thisComment.children[1].textContent = "Like"
                                    console.log("ceci est un unliked")
                                    setHeartColor("#BDBDBD")
                                    dispatch(unLikeCommentInTweet(tweetId,userId,commentId))
                                    if(setJustOnePonce){
                                        setJustOnePonce(true)
                                    }
                                }else{
                                    setHeartColor("#EB5757")
                                    console.log("ceci est un liked")
                                    thisComment.classList.add("liked")
                                    thisComment.children[1].textContent = "Liked"
                                    dispatch(likeCommentInTweet(tweetId,userId,commentId))
                                    if(setJustOnePonce){
                                        setJustOnePonce(true)
                                    }
                                }
                            }}>
                                <svg fill={heartColor} width="16.67" height="14.71" viewBox="0 -28 512.001 512" xmlns="http://www.w3.org/2000/svg">
                                    <path d="m256 455.515625c-7.289062 0-14.316406-2.640625-19.792969-7.4375-20.683593-18.085937-40.625-35.082031-58.21875-50.074219l-.089843-.078125c-51.582032-43.957031-96.125-81.917969-127.117188-119.3125-34.644531-41.804687-50.78125-81.441406-50.78125-124.742187 0-42.070313 14.425781-80.882813 40.617188-109.292969 26.503906-28.746094 62.871093-44.578125 102.414062-44.578125 29.554688 0 56.621094 9.34375 80.445312 27.769531 12.023438 9.300781 22.921876 20.683594 32.523438 33.960938 9.605469-13.277344 20.5-24.660157 32.527344-33.960938 23.824218-18.425781 50.890625-27.769531 80.445312-27.769531 39.539063 0 75.910156 15.832031 102.414063 44.578125 26.191406 28.410156 40.613281 67.222656 40.613281 109.292969 0 43.300781-16.132812 82.9375-50.777344 124.738281-30.992187 37.398437-75.53125 75.355469-127.105468 119.308594-17.625 15.015625-37.597657 32.039062-58.328126 50.167969-5.472656 4.789062-12.503906 7.429687-19.789062 7.429687zm-112.96875-425.523437c-31.066406 0-59.605469 12.398437-80.367188 34.914062-21.070312 22.855469-32.675781 54.449219-32.675781 88.964844 0 36.417968 13.535157 68.988281 43.882813 105.605468 29.332031 35.394532 72.960937 72.574219 123.476562 115.625l.09375.078126c17.660156 15.050781 37.679688 32.113281 58.515625 50.332031 20.960938-18.253907 41.011719-35.34375 58.707031-50.417969 50.511719-43.050781 94.136719-80.222656 123.46875-115.617188 30.34375-36.617187 43.878907-69.1875 43.878907-105.605468 0-34.515625-11.605469-66.109375-32.675781-88.964844-20.757813-22.515625-49.300782-34.914062-80.363282-34.914062-22.757812 0-43.652344 7.234374-62.101562 21.5-16.441406 12.71875-27.894532 28.796874-34.609375 40.046874-3.453125 5.785157-9.53125 9.238282-16.261719 9.238282s-12.808594-3.453125-16.261719-9.238282c-6.710937-11.25-18.164062-27.328124-34.609375-40.046874-18.449218-14.265626-39.34375-21.5-62.097656-21.5zm0 0" />
                                </svg>
                                <p style = {{color:heartColor}} className="griser">Like</p>
                            </div>
                            <div className="transistorTypeTwo"></div>
                            <div className="numberOfLikesOfComment">
                                {likes.length === 1 ? <p className="griser">{likes.length} Like</p> : <p className="griser">{likes.length} Likes</p>}
                            </div>
                        </div>
                    </div>
                </div>

            </div>
        </>
    )
}

export default Comment